import styled from "styled-components";
import { motion } from "framer-motion";

export const Icon = styled.svg`
  width: 100%;
  fill: #ff7008;

  &:hover path {
    fill: #fff;
  }
`;

export const StyledCircle = styled(motion.circle)``;

export const StyledHeart = styled(motion.svg)`
  stroke: #fff;
  cursor: pointer;
`;

export const MobileIcon = styled(motion.svg)`
  display: none;

  @media (max-width: 560px) {
    display: block;
  }
`;
