import React, { useState, useEffect } from "react";
import AddPost from "../AddPost";
import Calendar from "../Calendar";
import Home from "../Home";
import Notifications from "../Notifications";
import { posts } from "../posts";
import Sidebar from "../Sidebar";
import UserProfile from "../UserProfile";
import { LandingWrapper } from "./LandingWrapper";

function Landing({ loggedUser, setLoggedUser, user, setUser }) {
  const [isExpanded, setIsExpanded] = useState(false);
  const [changeComponent, setChangeComponent] = useState("house");
  const [post, setPost] = useState([]);
  const [notifications, setNotifications] = useState([
    {
      userID: 1,
      notificationsList: [
        {
          id: 1,
          desc: "Dodano nowy post",
          postID: "42de5f81-49a3-11ed-b422-d528f670e831",
          seen: false,
        },
        {
          id: 2,
          desc: "polubiło twój post",
          postID: "42de5f81-49a3-11ed-b422-d528f670e831",
          usersID: [80, 81],
          seen: false,
        },
        {
          id: 3,
          desc: "Dodano nowy post",
          postID: "42de5f81-49a3-11ed-b422-d528f670e832",
          seen: false,
        },
        {
          id: 4,
          desc: "Dodano nowy post",
          postID: "42de5f81-49a3-11ed-b422-d528f670e833",
          seen: false,
        },
      ],
    },
  ]);
  const [counter, setCounter] = useState(0);

  useEffect(() => {
    setPost(posts);
  }, []);

  useEffect(() => {
    count();
  }, [notifications]);

  const count = () => {
    const foundUser = notifications.find(
      (notification) => notification.userID === loggedUser.id
    );
    if (foundUser) {
      setCounter(
        foundUser.notificationsList.filter(
          (notification) => notification.seen === false
        ).length
      );
    }
  };

  const [windowWidth, setWindowWidth] = useState(window.innerWidth);
  const [openNav, setOpenNav] = useState(false);

  useEffect(() => {
    function handleResize() {
      setWindowWidth(window.innerWidth);
    }
    windowWidth >= 560 && setOpenNav(false);
    window.addEventListener("resize", handleResize);
    return () => window.removeEventListener("resize", handleResize);
  }, [windowWidth]);

  return (
    <LandingWrapper>
      <Sidebar
        isExpanded={isExpanded}
        setIsExpanded={setIsExpanded}
        setChangeComponent={setChangeComponent}
        loggedUser={loggedUser}
        counter={counter}
        windowWidth={windowWidth}
        openNav={openNav}
        setOpenNav={setOpenNav}
      />
      {!openNav
        ? (changeComponent === "house" && (
            <Home
              loggedUser={loggedUser}
              setPost={setPost}
              post={post}
              notifications={notifications}
              setNotifications={setNotifications}
            />
          )) ||
          (changeComponent === "bell" && (
            <Notifications
              loggedUser={loggedUser}
              notifications={notifications}
              setNotifications={setNotifications}
              setCounter={setCounter}
            />
          )) ||
          (changeComponent === "users" && (
            <Calendar loggedUser={loggedUser} user={user} />
          )) ||
          (changeComponent === "addPost" && (
            <AddPost
              loggedUser={loggedUser}
              setPost={setPost}
              post={post}
              notifications={notifications}
              setNotifications={setNotifications}
            />
          )) ||
          (changeComponent === "settings" && (
            <UserProfile
              loggedUser={loggedUser}
              setLoggedUser={setLoggedUser}
              user={user}
              setUser={setUser}
              setPost={setPost}
              post={post}
            />
          ))
        : null}
    </LandingWrapper>
  );
}

export default Landing;
