import { AnimatePresence } from "framer-motion";
import React from "react";
import { useEffect } from "react";
import { useState } from "react";
import { CheckIcon, TimesIcon } from "../Icons";
import { InputBox } from "../shared/InputBox";
import { StyledButton } from "../shared/StyledButton";
import { ErrorContainer } from "../RegisterLoginShared/ErrorContainer";
import { RegisterInputContainer } from "../RegisterLoginShared/InputContainer";
import { RegisterSection } from "../RegisterLoginShared/StyledSection";
import { StyledH2 } from "../RegisterLoginShared/StyledH2";
import { StyledInput } from "../RegisterLoginShared/StyledInput";
import {
  StyledSecondSpan,
  StyledSpan,
} from "../RegisterLoginShared/StyledSpan";
import { StyledHint } from "../RegisterLoginShared/StyledHint";
import { StyledError } from "../RegisterLoginShared/SyledError";
import { v1 as uuidv1 } from "uuid";

const USER_REGEX = /^[A-z][A-z0-9-_]{3,23}$/;
const PWD_REGEX = /^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#$%]).{8,24}$/;
const EMAIL_REGEX =
  /^[a-zA-Z.!#$%&'+/=?^_{|}~-][a-zA-Z0-9.!#$%&'+/=?^_{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;

function Register({
  user,
  setUser,
  setSuccess,
  setLoggedUser,
  changeComponent,
}) {
  const [userName, setUserName] = useState();
  const [userCheck, setUserCheck] = useState(false);
  const [userFocus, setUserFocus] = useState(false);

  const [email, setEmail] = useState();
  const [emailCheck, setEmailCheck] = useState(false);
  const [emailFocus, setEmailFocus] = useState(false);

  const [pwd, setPwd] = useState();
  const [pwdCheck, setPwdCheck] = useState(false);
  const [pwdFocus, setPwdFocus] = useState(false);

  const [pwdMatch, setPwdMatch] = useState();
  const [pwdMatchCheck, setPwdMatchCheck] = useState(false);
  const [pwdMatchFocus, setPwdMatchFocus] = useState(false);

  const [hint, setHint] = useState();

  const [errorMessage, setErrorMessage] = useState();

  useEffect(() => {
    if (userName !== undefined) {
      setUserCheck(USER_REGEX.test(userName));
    } else {
      setUserCheck(false);
    }
  }, [userName]);

  useEffect(() => {
    setPwdCheck(PWD_REGEX.test(pwd));
    setPwdMatchCheck(pwd === pwdMatch);
  }, [pwd, pwdMatch]);

  useEffect(() => {
    setEmailCheck(EMAIL_REGEX.test(email));
  }, [email]);

  const newUserId = uuidv1();

  const register = (e) => {
    e.preventDefault();
    const foundUser = user.find((item) => item.nick === userName);
    const foundUserEmail = user.find((item) => item.email === email);
    if (foundUser) {
      setErrorMessage("Nazwa użytkownika jest już zajęta.");
      setSuccess(false);
    } else {
      if (foundUserEmail) {
        setErrorMessage("Ten adres email jest już w uzyciu.");
        setSuccess(false);
      } else {
        setUser([
          ...user,
          {
            id: newUserId,
            nick: userName,
            password: pwd,
            email: email,
            image: "./img/avatarImg.png",
          },
        ]);
        setLoggedUser({
          id: newUserId,
          nick: userName,
          password: pwd,
          email: email,
          image: "./img/avatarImg.png",
        });
        setSuccess(true);
      }
    }
  };

  const section = {
    initial: { opacity: 0 },
    animate: { opacity: 1, tranistion: { duration: 1 } },
    exit: { x: "200vw", transition: { duration: 1 } },
  };

  return (
    <RegisterSection
      key="register"
      variants={section}
      initial="initial"
      animate="animate"
      exit="exit"
    >
      <StyledH2>Register</StyledH2>
      <RegisterInputContainer id="registerForm" onSubmit={register}>
        <InputBox grid={"1/3"}>
          <StyledInput
            type="text"
            id="text"
            onChange={(e) => setUserName(e.target.value)}
            onFocus={() => {
              setUserFocus(true);
              setHint(
                "Login musi zawierać od 4 do 24 znaków i musi zaczynć się od litery. Ltery, cyfry, podkreślniki i myślniki są dozwolone."
              );
            }}
            onBlur={() => setUserFocus(false)}
            required
          ></StyledInput>
          <StyledSpan>Wprowadź login</StyledSpan>
          <StyledSecondSpan
            style={{ display: userCheck && userName ? "block" : "none" }}
          >
            <CheckIcon />
          </StyledSecondSpan>
          <StyledSecondSpan
            style={{ display: userCheck || !userName ? "none" : "block" }}
          >
            <TimesIcon />
          </StyledSecondSpan>
        </InputBox>
        <InputBox grid={"1/3"}>
          <StyledInput
            type="email"
            id="email"
            onChange={(e) => setEmail(e.target.value)}
            onFocus={() => {
              setEmailFocus(true);
              setHint("Przykładowy email: name@domain.com");
            }}
            onBlur={() => setEmailFocus(false)}
            required
          ></StyledInput>
          <StyledSpan>Wprowadź adres email</StyledSpan>
          <StyledSecondSpan
            style={{ display: emailCheck && email ? "block" : "none" }}
          >
            <CheckIcon />
          </StyledSecondSpan>
          <StyledSecondSpan
            style={{ display: emailCheck || !email ? "none" : "block" }}
          >
            <TimesIcon />
          </StyledSecondSpan>
        </InputBox>
        <InputBox grid={"1/3"}>
          <StyledInput
            type="password"
            id="password"
            onChange={(e) => setPwd(e.target.value)}
            onFocus={() => {
              setPwdFocus(true);
              setHint(
                "Hasło musi zawierać od 8 do 24 znaków i składać się z conajmniej jednej dużej litery i znaku specjalnego. Dozwolone znaki specjalne: !, @, #"
              );
            }}
            onBlur={() => setPwdFocus(false)}
            required
          ></StyledInput>
          <StyledSpan>Wprowadź hasło</StyledSpan>
          <StyledSecondSpan style={{ display: pwdCheck ? "block" : "none" }}>
            <CheckIcon />
          </StyledSecondSpan>
          <StyledSecondSpan
            style={{ display: pwdCheck || !pwd ? "none" : "block" }}
          >
            <TimesIcon />
          </StyledSecondSpan>
        </InputBox>
        <InputBox grid={"1/3"}>
          <StyledInput
            type="password"
            id="matchPassword"
            onChange={(e) => setPwdMatch(e.target.value)}
            onFocus={() => {
              setPwdMatchFocus(true);
              setHint("Hasła sie nie zgadzają.");
            }}
            onBlur={() => setPwdMatchFocus(false)}
            required
          ></StyledInput>
          <StyledSpan>Powtórz hasło</StyledSpan>
          <StyledSecondSpan
            style={{ display: pwdMatchCheck && pwdMatch ? "block" : "none" }}
          >
            <CheckIcon />
          </StyledSecondSpan>
          <StyledSecondSpan
            style={{ display: pwdMatchCheck || !pwdMatch ? "none" : "block" }}
          >
            <TimesIcon />
          </StyledSecondSpan>
        </InputBox>
        <AnimatePresence mode="wait">
          <ErrorContainer>
            {(pwdFocus && !pwdCheck) ||
            (userFocus && !userCheck) ||
            (emailFocus && !emailCheck) ||
            (pwdMatchFocus && !pwdMatchCheck) ? (
              <StyledHint
                key="registerHint"
                initial={{ opacity: 0 }}
                animate={{ opacity: 1, transition: { duration: 0.2 } }}
                exit={{ opacity: 0, transition: { duration: 0.2 } }}
              >
                {hint}
              </StyledHint>
            ) : (
              <></>
            )}
            <StyledError
              key="registerErrorMessage"
              style={{
                display:
                  pwdFocus || userFocus || emailFocus || pwdMatchFocus
                    ? "none"
                    : "block",
              }}
            >
              {errorMessage}
            </StyledError>
          </ErrorContainer>
        </AnimatePresence>
      </RegisterInputContainer>
      <StyledButton
        color={"#FF7008"}
        backgroundColor={"#fff"}
        fontWeight={"200"}
        justify={"end"}
        onClick={() => changeComponent("login")}
      >
        Logowanie
      </StyledButton>
      <StyledButton
        form="registerForm"
        type="submit"
        color={"#fff"}
        backgroundColor={"#FF7008"}
        fontWeight={"300"}
        justify={"start"}
        disabled={!userCheck || !pwdCheck ? true : false}
      >
        Zarejestruj
      </StyledButton>
    </RegisterSection>
  );
}

export default Register;
