import styled from "styled-components";

export const LoginInputContainer = styled.form`
  grid-column: 1/3;
  grid-template-columns: 1fr 1fr;
  display: grid;
  grid-row-gap: var(--login-div-grid-row-gap);
  width: 80%;
  grid-column-gap: 1rem;
  height: 22.188rem;
`;

export const RegisterInputContainer = styled(LoginInputContainer)`
  grid-row-gap: var(--register-div-grid-row-gap);
  height: 25.938rem;
  @media (max-width: 420px) {
    grid-row-gap: var(--media-register-div-grid-row-gap);
  }
`;

export const UserProfileInputContainer = styled(LoginInputContainer)`
  width: 90%;
  margin: auto;
  height: 40rem;
  @media (max-width: 560px) {
    grid-row-gap: 1rem;
    height: 45rem;
  }
`;
