import styled from "styled-components";

export const InputBox = styled.div`
  grid-column: ${(props) => props.grid};
  position: relative;
  margin: auto;

  input:valid ~ span:first-of-type,
  input:focus ~ span:first-of-type,
  textarea:valid ~ span:first-of-type,
  textarea:focus ~ span:first-of-type {
    top: 0;
    transform: translateX(10px) translateY(-10px);
    font-size: 1rem;
    background-color: #fff;
    border-left: 1px solid var(--button-color);
    border-right: 1px solid var(--button-color);
    letter-spacing: 0.1rem;
    padding: 0 10px;
  }

  input:valid,
  input:focus,
  textarea:valid,
  textarea:focus {
    border-color: var(--button-color);
    outline: none;
  }

  @media (max-width: 560px) {
    grid-column: 1/3;
  }
`;

export const SettingsInputBox = styled(InputBox)`
  width: 100%;
`;
