import styled from "styled-components";

export const NotificationsContainer = styled.div`
  display: flex;
  height: 80vh;
  overflow: scroll;
  overflow-x: hidden;
  flex-direction: column;
  flex-wrap: nowrap;
  align-items: center;
  grid-column: 1/3;
  &::-webkit-scrollbar-thumb {
    background: #ffc297;
    border-radius: 25px;
  }

  &::-webkit-scrollbar {
    width: 20px;
  }
`;

export const UsersContainer = styled(NotificationsContainer)`
  height: 75vh;
`;
