import styled from "styled-components";

export const FileLabel = styled.label`
  display: block;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background-image: url(${(props) => props.image});
  background-position: center;
  background-size: cover;
  grid-column: 2/3;
  justify-self: end;
`;
