import styled from "styled-components";

export const StyledInput = styled.input`
  font-family: "Roboto";
  font-weight: 200;
  font-size: var(--input-fs);
  height: 3.125rem;
  border-radius: 5px;
  border: 1px solid var(--unactive-text-color);
  transition: 0.5s;
  padding: 10px;
  width: 300px;
  @media (max-width: 420px) {
    width: 260px;
  }
`;

export const StyledTextArea = styled.textarea`
  font-family: "Roboto";
  font-weight: 200;
  font-size: var(--input-fs);
  height: 3.125rem;
  min-width: 100%;
  border-radius: 5px;
  border: 1px solid var(--unactive-text-color);
  transition: 0.5s;
  padding: 10px;
  resize: none;
  &::-webkit-scrollbar-thumb {
    background: #ffc297;
    border-radius: 25px;
  }

  &::-webkit-scrollbar {
    width: 20px;
  }
`;

export const SettingsInput = styled(StyledInput)`
  width: 100%;
`;

export const FileInput = styled(StyledInput)`
  display: none;
`;
