import React, { useState } from "react";
import { ListItem } from "./ListItem";
import { StyledNav } from "./StyledNav";
import { StyledUl } from "./StyledUl";
import {
  HouseIcon,
  BellIcon,
  UsersIcon,
  ChartsIcon,
  SettingsIcon,
} from "../Icons/index";
import { StyledImg } from "../shared/StyledImg";
import { Counter } from "./Counter";
import BurgerIcon from "../Icons/BurgerIcon";
import { BurgerButton } from "../shared/StyledButton";
import { AnimatePresence } from "framer-motion";

function Sidebar({
  setChangeComponent,
  loggedUser,
  counter,
  windowWidth,
  openNav,
  setOpenNav,
}) {
  const [activeIcon, setActiveIcon] = useState(true);
  const [clickedIcon, setClickedIcon] = useState("house");

  const handleIconClick = (e) => {
    setActiveIcon(true);
    setClickedIcon(e.currentTarget.id);
  };

  const mobileNav = {
    initial: { height: "5vh" },
    animate: { height: "100vh", transition: { duration: 0.4 } },
  };

  return (
    <>
      {windowWidth >= 560 ? (
        <StyledNav>
          <StyledUl>
            <ListItem>
              <StyledImg
                image={loggedUser.image}
                alt="avatar"
                style={{ margin: "auto" }}
              ></StyledImg>
            </ListItem>
            <ListItem
              id="house"
              onClick={(e) => {
                handleIconClick(e);
                setChangeComponent("house");
              }}
            >
              <HouseIcon clickedIcon={clickedIcon} activeIcon={activeIcon} />
            </ListItem>
            <ListItem
              id="bell"
              onClick={(e) => {
                handleIconClick(e);
                setChangeComponent("bell");
              }}
            >
              <BellIcon clickedIcon={clickedIcon} activeIcon={activeIcon} />
              {counter !== 0 && <Counter>{counter}</Counter>}
            </ListItem>
            <ListItem
              id="users"
              onClick={(e) => {
                handleIconClick(e);
                setChangeComponent("users");
              }}
            >
              <UsersIcon clickedIcon={clickedIcon} activeIcon={activeIcon} />
            </ListItem>
            <ListItem
              id="charts"
              onClick={(e) => {
                handleIconClick(e);
                setChangeComponent("addPost");
              }}
            >
              <ChartsIcon clickedIcon={clickedIcon} activeIcon={activeIcon} />
            </ListItem>
            <ListItem
              id="settings"
              onClick={(e) => {
                handleIconClick(e);
                setChangeComponent("settings");
              }}
            >
              <SettingsIcon clickedIcon={clickedIcon} activeIcon={activeIcon} />
            </ListItem>
          </StyledUl>
        </StyledNav>
      ) : (
        <AnimatePresence>
          <StyledNav
            variants={mobileNav}
            animate={openNav ? "animate" : "initial"}
            style={{ backgroundColor: openNav ? "#FFC297" : "transparent" }}
          >
            <BurgerButton onClick={() => setOpenNav(!openNav)}>
              <BurgerIcon openNav={openNav} />
            </BurgerButton>
            <StyledUl style={{ display: openNav ? "flex" : "none" }}>
              <ListItem>
                <StyledImg
                  image={loggedUser.image}
                  alt="avatar"
                  style={{ margin: "auto" }}
                ></StyledImg>
              </ListItem>
              <ListItem
                id="house"
                onClick={(e) => {
                  handleIconClick(e);
                  setChangeComponent("house");
                  setOpenNav(false);
                }}
              >
                <HouseIcon clickedIcon={clickedIcon} activeIcon={activeIcon} />
              </ListItem>
              <ListItem
                id="bell"
                onClick={(e) => {
                  handleIconClick(e);
                  setChangeComponent("bell");
                  setOpenNav(false);
                }}
              >
                <BellIcon clickedIcon={clickedIcon} activeIcon={activeIcon} />
                {counter !== 0 && <Counter>{counter}</Counter>}
              </ListItem>
              <ListItem
                id="users"
                onClick={(e) => {
                  handleIconClick(e);
                  setChangeComponent("users");
                  setOpenNav(false);
                }}
              >
                <UsersIcon clickedIcon={clickedIcon} activeIcon={activeIcon} />
              </ListItem>
              <ListItem
                id="charts"
                onClick={(e) => {
                  handleIconClick(e);
                  setChangeComponent("addPost");
                  setOpenNav(false);
                }}
              >
                <ChartsIcon clickedIcon={clickedIcon} activeIcon={activeIcon} />
              </ListItem>
              <ListItem
                id="settings"
                onClick={(e) => {
                  handleIconClick(e);
                  setChangeComponent("settings");
                  setOpenNav(false);
                }}
              >
                <SettingsIcon
                  clickedIcon={clickedIcon}
                  activeIcon={activeIcon}
                />
              </ListItem>
            </StyledUl>
          </StyledNav>
        </AnimatePresence>
      )}
    </>
  );
}

export default Sidebar;
