import styled from "styled-components";

export const NewPost = styled.form`
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-items: end;
  align-items: center;
  align-content: space-evenly;
  grid-column-gap: 1rem;
  grid-row-gap: 1rem;

  @media (max-width: 560px) {
    grid-template-columns: 1fr;
    grid-row-gap: 2rem;

    i {
      display: none;
    }

    div {
      grid-column: 1;
    }
  }
`;
