import styled from "styled-components";

export const PostCardContainer = styled.div`
  grid-column: 1/3;
  width: 100%;
  height: 80vh;
  overflow: scroll;
  overflow-x: hidden;
  &::-webkit-scrollbar-thumb {
    background: #ffc297;
    border-radius: 25px;
  }

  &::-webkit-scrollbar {
    width: 20px;
  }
`;
