import React, { useState } from "react";
import { CardHeading } from "../Home/CardHeading";
import { CardText } from "../Home/CardText";
import { CardTitle } from "../Home/CardTitle";
import { PostCard } from "../Home/PostCard";
import { PencilIconCard, TrashIcon } from "../Icons";
import {
  SettingsInput,
  StyledInput,
  StyledTextArea,
} from "../RegisterLoginShared/StyledInput";
import { StyledSpan } from "../RegisterLoginShared/StyledSpan";
import { InputBox } from "../shared/InputBox";
import { CardButton, StyledButton } from "../shared/StyledButton";
import { StyledH2 } from "../shared/StyledH2";
import { StyledImg } from "../shared/StyledImg";
import { StyledSection } from "../shared/StyledSection";
import { CardOptions } from "./CardOptions";
import { CreatePostContainer } from "./CreatePostContainer";
import { NewPost } from "./NewPost";
import { YourPosts } from "./YourPosts";
import { v1 as uuidv1 } from "uuid";
import { AnimatePresence } from "framer-motion";

function AddPost({
  loggedUser,
  setPost,
  post,
  setNotifications,
  notifications,
}) {
  const [title, setTitle] = useState("");
  const [text, setText] = useState("");
  const [newID, setNewID] = useState("");
  const [editing, setEditing] = useState(false);

  const foundUser = notifications.find(
    (notification) => notification.userID === loggedUser.id
  );

  const newPostID = uuidv1();
  const notificationID = uuidv1();

  const addPost = (e) => {
    e.preventDefault();
    setPost([
      ...post,
      {
        id: newPostID,
        userID: loggedUser.id,
        title: title,
        text: text,
        image: loggedUser.image,
      },
    ]);

    if (foundUser) {
      setNotifications(
        notifications.map((notification) => {
          return notification.userID === foundUser.userID
            ? {
                userID: foundUser.userID,
                notificationsList: [
                  ...foundUser.notificationsList,
                  {
                    id: notificationID,
                    desc: "Dodano nowy post",
                    postID: newPostID,
                    seen: false,
                  },
                ],
              }
            : notification;
        })
      );
    } else {
      setNotifications([
        ...notifications,
        {
          userID: loggedUser.id,
          notificationsList: [
            {
              id: notificationID,
              desc: "Dodano nowy post",
              postID: newPostID,
              seen: false,
            },
          ],
        },
      ]);
    }

    setTitle("");
    setText("");
  };

  const editPost = (e) => {
    e.preventDefault();
    setPost(
      post.map((item) => {
        return item.id === newID
          ? {
              id: item.id,
              userID: item.userID,
              title: title,
              text: text,
              image: item.image,
            }
          : item;
      })
    );
    setNewID("");
    setTitle("");
    setText("");
    setEditing(false);
  };

  const handleRemove = (id) => {
    setPost(post.filter((item) => item.id !== id));
    setNotifications(
      notifications.map((notification) => {
        return notification.userID === loggedUser.id
          ? {
              userID: notification.userID,
              notificationsList: notification.notificationsList.filter(
                (item) => item.postID !== id
              ),
            }
          : notification;
      })
    );
  };

  const handleEdit = (id, title, text) => {
    setTitle(title);
    setText(text);
    setNewID(id);
  };

  return (
    <AnimatePresence>
      <StyledSection
        key="addpost"
        initial={{ x: "-200vw", transition: { duration: 2 } }}
        animate={{ x: 0, transition: { duration: 0.4 } }}
      >
        <StyledH2>Dodaj post</StyledH2>
        <CreatePostContainer>
          <NewPost id="newPost" onSubmit={editing ? editPost : addPost}>
            <InputBox grid={"1/2"} style={{ width: "100%" }}>
              <SettingsInput
                type="text"
                id="title"
                value={title}
                onChange={(e) => setTitle(e.target.value)}
                required
              />
              <StyledSpan>Dodaj tytuł</StyledSpan>
            </InputBox>
            <StyledImg
              image={loggedUser.image}
              alt="avatar"
              style={{ gridColumns: "2/3" }}
            ></StyledImg>
            <InputBox grid={"1/3"} style={{ width: "100%" }}>
              <StyledTextArea
                type="textfield"
                id="text"
                value={text}
                style={{ height: "10vh" }}
                onChange={(e) => setText(e.target.value)}
                required
              />
              <StyledSpan>Dodaj tekst</StyledSpan>
            </InputBox>
            <StyledButton
              color={"#FF7008"}
              backgroundColor={"#fff"}
              fontWeight={"200"}
              style={{ margin: "auto" }}
              onClick={() => {
                setTitle("");
                setText("");
              }}
            >
              Anuluj
            </StyledButton>
            <StyledButton
              form="newPost"
              type="submit"
              color={"#fff"}
              backgroundColor={"#FF7008"}
              fontWeight={"300"}
              style={{ margin: "auto" }}
            >
              Dodaj
            </StyledButton>
          </NewPost>
          <StyledH2 style={{ marginTop: "2rem" }}>Zarządzaj postami</StyledH2>
          <YourPosts>
            {post
              .slice(0)
              .reverse()
              .map((item, i) => {
                return (
                  item.userID === loggedUser.id && (
                    <PostCard
                      key={i}
                      id={item.id}
                      initial={{ x: "-100vw" }}
                      animate={{
                        x: 0,
                        transition: {
                          delay: i * 0.2,
                          duration: 0.1,
                          type: "spring",
                          stiffness: 100,
                        },
                      }}
                    >
                      <CardHeading>
                        <CardTitle>{item.title}</CardTitle>
                        <StyledImg
                          margin={"0 0 0 auto"}
                          image={item.image}
                          alt="avatar"
                        ></StyledImg>
                      </CardHeading>
                      <CardText>{item.text}</CardText>
                      <CardOptions>
                        <CardButton
                          onClick={() => {
                            handleEdit(item.id, item.title, item.text);
                            setEditing(true);
                          }}
                        >
                          <PencilIconCard />
                        </CardButton>
                        <CardButton onClick={() => handleRemove(item.id)}>
                          <TrashIcon />
                        </CardButton>
                      </CardOptions>
                    </PostCard>
                  )
                );
              })}
          </YourPosts>
        </CreatePostContainer>
      </StyledSection>
    </AnimatePresence>
  );
}

export default AddPost;
