import styled from "styled-components";

export const StyledImg = styled.i`
  display: block;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background-image: url(${(props) => props.image});
  background-position: center;
  background-size: cover;
  margin: ${(props) => props.margin};
  justify-self: end;
`;
