import { AnimatePresence } from "framer-motion";
import React from "react";
import { useEffect } from "react";
import { useState } from "react";
import { CheckIcon, TimesIcon } from "../Icons";
import { InputBox } from "../shared/InputBox";
import { StyledButton } from "../shared/StyledButton";
import { ErrorContainer } from "../RegisterLoginShared/ErrorContainer";
import { LoginInputContainer } from "../RegisterLoginShared/InputContainer";
import { LoginSection } from "../RegisterLoginShared/StyledSection";
import { StyledH2 } from "../RegisterLoginShared/StyledH2";
import { StyledInput } from "../RegisterLoginShared/StyledInput";
import {
  StyledSecondSpan,
  StyledSpan,
} from "../RegisterLoginShared/StyledSpan";
import { StyledHint } from "../RegisterLoginShared/StyledHint";
import { StyledError } from "../RegisterLoginShared/SyledError";

const USER_REGEX = /^[A-z][A-z0-9-_]{3,23}$/;
const PWD_REGEX = /^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#$%]).{8,24}$/;

function Login({ user, setSuccess, setLoggedUser, changeComponent }) {
  const [userName, setUserName] = useState();
  const [userCheck, setUserCheck] = useState(false);
  const [userFocus, setUserFocus] = useState(false);

  const [pwd, setPwd] = useState();
  const [pwdCheck, setPwdCheck] = useState(false);
  const [pwdFocus, setPwdFocus] = useState(false);

  const [hint, setHint] = useState();

  const [errorMessage, setErrorMessage] = useState();

  useEffect(() => {
    if (userName !== undefined) {
      setUserCheck(USER_REGEX.test(userName));
    } else {
      setUserCheck(false);
    }
  }, [userName]);

  useEffect(() => {
    setPwdCheck(PWD_REGEX.test(pwd));
  }, [pwd]);

  const login = (e) => {
    e.preventDefault();
    const foundUser = user.find((item) => item.nick === userName);
    if (foundUser) {
      if (foundUser.password === pwd) {
        setLoggedUser(foundUser);
        setErrorMessage();
        setSuccess(true);
      } else {
        setErrorMessage("Błędne hasło");
        setSuccess(false);
      }
    } else {
      setErrorMessage("nie znaleziono takiego użytkownika");
      setSuccess(false);
    }
  };

  const section = {
    initial: { opacity: 0 },
    animate: { opacity: 1, tranistion: { duration: 1 } },
    exit: { x: "200vw", transition: { duration: 1 } },
  };

  return (
    <AnimatePresence>
      <LoginSection
        key="login"
        variants={section}
        initial="initial"
        animate="animate"
        exit="exit"
      >
        <StyledH2>Login</StyledH2>
        <LoginInputContainer id="loginForm" onSubmit={login}>
          <InputBox grid={"1 / 3"}>
            <StyledInput
              type="text"
              id="text"
              onChange={(e) => setUserName(e.target.value)}
              onFocus={() => {
                setUserFocus(true);
                setHint(
                  "Login składa się z 24 znaków i musi zaczynć się od litery. Ltery, cyfry, podkreślniki i myślniki są dozwolone."
                );
              }}
              onBlur={() => setUserFocus(false)}
              required
            ></StyledInput>
            <StyledSpan>Wprowadź login</StyledSpan>
            <StyledSecondSpan
              style={{ display: userCheck && userName ? "block" : "none" }}
            >
              <CheckIcon />
            </StyledSecondSpan>
            <StyledSecondSpan
              style={{ display: userCheck || !userName ? "none" : "block" }}
            >
              <TimesIcon />
            </StyledSecondSpan>
          </InputBox>
          <InputBox grid={"1 / 3"}>
            <StyledInput
              type="password"
              id="password"
              onChange={(e) => setPwd(e.target.value)}
              onFocus={() => {
                setPwdFocus(true);
                setHint(
                  "Hasło musi zawierać od 8 do 24 znaków i składać się z conajmniej jednej dużej litery i znaku specjalnego. Dozwolone znaki specjalne: !, @, #"
                );
              }}
              onBlur={() => setPwdFocus(false)}
              required
            ></StyledInput>
            <StyledSpan>Wprowadź hasło</StyledSpan>
            <StyledSecondSpan style={{ display: pwdCheck ? "block" : "none" }}>
              <CheckIcon />
            </StyledSecondSpan>
            <StyledSecondSpan
              style={{ display: pwdCheck || !pwd ? "none" : "block" }}
            >
              <TimesIcon />
            </StyledSecondSpan>
          </InputBox>
          <ErrorContainer>
            <AnimatePresence mode="wait">
              {(pwdFocus && !pwdCheck) || (userFocus && !userCheck) ? (
                <StyledHint
                  key="LoginHint"
                  initial={{ opacity: 0, transition: { duration: 0.2 } }}
                  animate={{ opacity: 1, transition: { duration: 0.2 } }}
                  exit={{ opacity: 0, transition: { duration: 0.2 } }}
                >
                  {hint}
                </StyledHint>
              ) : (
                <></>
              )}
              <StyledError
                key="loginErrorMessage"
                style={{ display: pwdFocus || userFocus ? "none" : "block" }}
              >
                {errorMessage}
              </StyledError>
            </AnimatePresence>
          </ErrorContainer>
        </LoginInputContainer>
        <StyledButton
          color={"#FF7008"}
          backgroundColor={"#fff"}
          fontWeight={"200"}
          justify={"end"}
          onClick={() => changeComponent("register")}
        >
          Rejestracja
        </StyledButton>
        <StyledButton
          form="loginForm"
          type="submit"
          color={"#fff"}
          backgroundColor={"#FF7008"}
          fontWeight={"300"}
          justify={"start"}
          disabled={!userCheck || !pwdCheck ? true : false}
        >
          Zaloguj
        </StyledButton>
      </LoginSection>
    </AnimatePresence>
  );
}

export default Login;
