import React from "react";
import { MobileIcon } from "./StyledSvg";
import { motion } from "framer-motion";

const firstLine = {
  initial: { y: 0 },
  end: { y: "10px", rotate: 45 },
};

const middleLine = {
  visable: { opacity: 1, x: 0 },
  hide: { opacity: 0, x: "10vw" },
};

const lastLine = {
  initial: { y: 0 },
  end: { y: "-10px", rotate: -45 },
};

function BurgerIcon({ openNav }) {
  return (
    <MobileIcon
      width="40"
      height="40"
      viewBox="0 0 40 40"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
    >
      <motion.line
        variants={firstLine}
        animate={openNav ? "end" : "initial"}
        x1="6.5"
        y1="8.5"
        x2="33.5"
        y2="8.5"
        stroke="#FF7008"
        strokeWidth="3"
        strokeLinecap="round"
      />
      <motion.line
        variants={middleLine}
        animate={openNav ? "hide" : "visable"}
        x1="6.5"
        y1="18.5"
        x2="33.5"
        y2="18.5"
        stroke="#FF7008"
        strokeWidth="3"
        strokeLinecap="round"
      />
      <motion.line
        variants={lastLine}
        animate={openNav ? "end" : "initial"}
        x1="6.5"
        y1="29.5"
        x2="33.5"
        y2="29.5"
        stroke="#FF7008"
        strokeWidth="3"
        strokeLinecap="round"
      />
    </MobileIcon>
  );
}

export default BurgerIcon;
