import styled from "styled-components";
import { motion } from "framer-motion";

export const StyledHint = styled(motion.p)`
  font-family: "Roboto";
  font-weight: 200;
  font-size: var(--hint-fs);
  text-align: left;
  letter-spacing: 0.8px;
  border: 1px solid var(--button-color);
  border-radius: 5px;
  padding: 10px;
  transition: 0.5;
`;
