import React from "react";
import { StyledH2 } from "../shared/StyledH2";
import { StyledSection } from "../shared/StyledSection";
import { StyledImg } from "../shared/StyledImg";
import { CardHeading } from "./CardHeading";
import { CardText } from "./CardText";
import { CardTitle } from "./CardTitle";
import { PostCard } from "./PostCard";
import { PostCardContainer } from "./PostCardContianer";
import { HeartIcon } from "../Icons";
import { LikesContainer } from "./LikesContainer";
import { AnimatePresence } from "framer-motion";

function Home({ loggedUser, post, notifications, setNotifications }) {
  const handleLike = (e) => {
    const currentTarget = e.currentTarget.id;
    const foundPostUser = post.find((item) => item.id === currentTarget);
    const foundNotifications = notifications.find(
      (notification) => notification.userID === foundPostUser.userID
    );
    const foundUsersID = foundNotifications.notificationsList.find(
      (item) =>
        item.desc === "polubiło twój post" && item.postID === currentTarget
    );
    const foundPostLike = foundNotifications.notificationsList.find(
      (item) =>
        item.desc === "polubiło twój post" && item.postID === currentTarget
    );
    if (foundNotifications) {
      if (foundPostLike) {
        if (!foundPostLike.usersID.includes(loggedUser.id)) {
          setNotifications(
            notifications.map((notification) => {
              return notification.userID === foundNotifications.userID
                ? {
                    userID: foundNotifications.userID,
                    notificationsList: foundNotifications.notificationsList.map(
                      (item) => {
                        return item.postID === currentTarget &&
                          item.desc === "polubiło twój post"
                          ? {
                              id: item.id,
                              desc: item.desc,
                              postID: item.postID,
                              usersID: [...foundUsersID.usersID, loggedUser.id],
                              seen: false,
                            }
                          : item;
                      }
                    ),
                  }
                : notification;
            })
          );
        } else {
          setNotifications(
            notifications.map((notification) => {
              return notification.userID === foundNotifications.userID
                ? {
                    userID: foundNotifications.userID,
                    notificationsList: foundNotifications.notificationsList.map(
                      (item) => {
                        return item.postID === currentTarget &&
                          item.desc === "polubiło twój post"
                          ? {
                              id: item.id,
                              desc: item.desc,
                              postID: item.postID,
                              usersID: item.usersID.filter(
                                (item) => item !== loggedUser.id
                              ),
                              seen: true,
                            }
                          : item;
                      }
                    ),
                  }
                : notification;
            })
          );
        }
      } else {
        setNotifications(
          notifications.map((notification) => {
            return notification.userID === foundNotifications.userID
              ? {
                  userID: notification.userID,
                  notificationsList: [
                    ...notification.notificationsList,
                    {
                      id: notification.notificationsList.length + 1,
                      desc: "polubiło twój post",
                      postID: currentTarget,
                      usersID: [loggedUser.id],
                      seen: false,
                    },
                  ],
                }
              : notification;
          })
        );
      }
    }
  };

  const container = {
    initial: { x: "-200vw" },
    animate: { x: 0, transition: { duration: 0.4 } },
  };

  return (
    <AnimatePresence>
      <StyledSection
        key="homeSection"
        variants={container}
        initial="initial"
        animate="animate"
      >
        <StyledH2>Posty</StyledH2>
        <StyledImg
          image={loggedUser.image}
          alt="avatar"
          style={{ gridColumns: "2/3" }}
        ></StyledImg>
        <PostCardContainer>
          {[...post].reverse().map((item, i) => {
            const number = item.id;
            const foundPostNotifications = notifications.find(
              (notification) =>
                notification.userID ===
                post.find((item) => item.id === number).userID
            );
            return (
              <PostCard
                key={i}
                id={item.id}
                initial={{ x: "-100vw" }}
                animate={{
                  x: 0,
                  transition: {
                    delay: i * 0.2,
                    duration: 0.1,
                    type: "spring",
                    stiffness: 100,
                  },
                }}
                onDoubleClick={(e) => handleLike(e)}
              >
                <CardHeading>
                  <CardTitle>{item.title}</CardTitle>
                  <StyledImg
                    margin={"0 0 0 auto"}
                    image={item.image}
                    alt="avatar"
                  ></StyledImg>
                </CardHeading>
                <CardText>{item.text}</CardText>
                {foundPostNotifications.notificationsList.map((item) => {
                  return item.postID === number &&
                    item.desc === "polubiło twój post" ? (
                    <LikesContainer key={number}>
                      <HeartIcon />
                      <span>{item.usersID.length}</span>
                    </LikesContainer>
                  ) : null;
                })}
              </PostCard>
            );
          })}
        </PostCardContainer>
      </StyledSection>
    </AnimatePresence>
  );
}

export default Home;
