import styled from "styled-components";

export const CardOptions = styled.div`
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-evenly;
  align-items: center;
  border-radius: 5px;

  &:hover {
    background-color: rgba(0, 0, 0, 0.6);
  }
  &:hover button {
    display: block;
    cursor: pointer;
  }
`;
