import React from "react";
import { AnimatePresence } from "framer-motion";
import { StyledH2 } from "../shared/StyledH2";
import { StyledSection } from "../shared/StyledSection";
import { StyledImg } from "../shared/StyledImg";
import { UsersContainer } from "../shared/NotificationsContainer";
import { UserCard } from "./UserCard";
import { CardTitle } from "../Home/CardTitle";
import { CardText } from "../Home/CardText";

function Calendar({ user, loggedUser }) {
  return (
    <AnimatePresence>
      <StyledSection
        key="calendar"
        initial={{ x: "-200vw", transition: { duration: 2 } }}
        animate={{ x: 0, transition: { duration: 0.4 } }}
      >
        <StyledH2>Wszyscy użytkownicy</StyledH2>
        <StyledImg
          image={loggedUser.image}
          alt="avatar"
          style={{ gridColumn: "2/3" }}
        ></StyledImg>
        <UsersContainer>
          {user.map((person) => {
            return (
              <UserCard key={person.id}>
                <CardTitle>
                  {person.firstName === undefined ||
                  person.lastName === undefined ? (
                    <>brak informacji</>
                  ) : (
                    <>
                      {person.firstName} {person.lastName}
                    </>
                  )}
                </CardTitle>
                <CardText style={{ marginBottom: "0" }}>{person.nick}</CardText>
                <StyledImg image={person.image} alt="avatar"></StyledImg>
              </UserCard>
            );
          })}
        </UsersContainer>
      </StyledSection>
    </AnimatePresence>
  );
}

export default Calendar;
