import { AnimatePresence } from "framer-motion";
import React, { useEffect } from "react";
import { HeartIcon, PencilIconCard } from "../Icons";
import { StyledH2 } from "../shared/StyledH2";
import { StyledImg } from "../shared/StyledImg";
import { NotificationSection } from "../shared/StyledSection";
import { Notification } from "./Notification";
import { NotificationsContainer } from "../shared/NotificationsContainer";
import { StyledParagraph } from "./StyledParagraph";

function Notifications({
  notifications,
  loggedUser,
  setNotifications,
  setCounter,
}) {
  useEffect(() => {
    const check = () => {
      const foundUser = notifications.find(
        (notification) => notification.userID === loggedUser.id
      );
      if (foundUser) {
        setNotifications(
          notifications.map((item) => {
            return item.userID === foundUser.userID
              ? {
                  userID: foundUser.userID,
                  notificationsList: foundUser.notificationsList.map(
                    (notification) => {
                      return notification.desc === "Dodano nowy post"
                        ? {
                            id: notification.id,
                            desc: notification.desc,
                            postID: notification.postID,
                            seen: true,
                          }
                        : {
                            id: notification.id,
                            desc: notification.desc,
                            postID: notification.postID,
                            usersID: notification.usersID,
                            seen: true,
                          };
                    }
                  ),
                }
              : item;
          })
        );
        setCounter(0);
      }
    };
    check();
  }, []);

  return (
    <AnimatePresence>
      <NotificationSection
        key="notifi"
        initial={{ x: "-200vw", transition: { duration: 2 } }}
        animate={{ x: 0, transition: { duration: 0.4 } }}
      >
        <StyledH2>Powiadomienia</StyledH2>
        <StyledImg
          image={loggedUser.image}
          alt="avatar"
          style={{ gridColumns: "2/3" }}
        ></StyledImg>
        <NotificationsContainer>
          {notifications.map((item) => {
            return item.userID === loggedUser.id
              ? item.notificationsList
                  .slice(0)
                  .reverse()
                  .map((notification, i) => {
                    return (
                      <Notification
                        key={i}
                        id={i}
                        initial={{ x: "-200vw" }}
                        animate={{
                          x: 0,
                          transition: {
                            delay: i * 0.2,
                            duration: 0.1,
                            type: "spring",
                            stiffness: 100,
                          },
                        }}
                      >
                        {notification.desc === "polubiło twój post" ? (
                          <StyledParagraph>
                            {notification.usersID.length}

                            {notification.usersID.length === 1 ? (
                              <> użytkownik polubił twój post</>
                            ) : (
                              <> użytkwoników polubiło twój post</>
                            )}
                            <HeartIcon />
                          </StyledParagraph>
                        ) : (
                          <StyledParagraph>
                            {notification.desc}
                            <PencilIconCard />
                          </StyledParagraph>
                        )}
                      </Notification>
                    );
                  })
              : null;
          })}
        </NotificationsContainer>
      </NotificationSection>
    </AnimatePresence>
  );
}

export default Notifications;
