import { createGlobalStyle } from "styled-components";
import RobotoRegular from "../fonts/Roboto-Regular.ttf";
import RobotoMedium from "../fonts/Roboto-Medium.ttf";
import RobotoBold from "../fonts/Roboto-Bold.ttf";
import RobotoBlack from "../fonts/Roboto-Black.ttf";

export const GlobalStyles = createGlobalStyle`

@font-face{
  font-family: "Roboto";
  src: local("Roboto"), url(${RobotoRegular}) format("truetype");
  font-weight: 100;
}

@font-face{
  font-family: "Roboto";
  src: local("Roboto"), url(${RobotoMedium}) format("truetype");
  font-weight: 200;
}

@font-face{
  font-family: "Roboto";
  src: local("Roboto"), url(${RobotoBold}) format("truetype");
  font-weight: 300;
}

@font-face{
  font-family: "Roboto";
  src: local("Roboto"), url(${RobotoBlack}) format("truetype");
  font-weight: 400;
}

:root{
    --saidbar-color-bg: #FFC297;
    --button-color: #FF7008;
    --active-text-color: #1C1C1C;
    --unactive-text-color: #858585;
    --h2-fs: 2rem;
    --expandable-li-fs: 1.75rem;
    --main-h2: 2.813rem;
    --button-fs: 1.75rem;
    --grid-column-gap: 3.125rem;
    --media-grid-column-gap: 1.25rem;
    --input-fs: 1.25rem;
    --login-div-grid-row-gap: 3.125rem;
    --button-width: 10.625rem;
    --input-width: 21.625rem;
    --error-fs: 2rem;
    --hint-fs: 1.125rem;
    --register-div-grid-row-gap: 1.875rem;
    --media-register-div-grid-row-gap: 1.25rem;
    --card-heading-fs: 1.5rem;
    --card-text-fs: 1.125rem;
}

*, ::before, ::after{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    
}
`;
