import React, { useState, useEffect } from "react";
import { SettingsInputBox } from "../shared/InputBox";
import { StyledButton } from "../shared/StyledButton";
import { StyledH2 } from "../shared/StyledH2";
import { UserProfileSection } from "./UserProfileSection";
import { StyledImg } from "../shared/StyledImg";
import {
  StyledSecondSpan,
  StyledSpan,
} from "../RegisterLoginShared/StyledSpan";
import { CheckIcon, TimesIcon } from "../Icons";
import { UserProfileInputContainer } from "../RegisterLoginShared/InputContainer";
import { FileInput, SettingsInput } from "../RegisterLoginShared/StyledInput";
import { StyledHint } from "../RegisterLoginShared/StyledHint";
import { StyledError } from "../RegisterLoginShared/SyledError";
import { ErrorContainer } from "../RegisterLoginShared/ErrorContainer";
import { AnimatePresence } from "framer-motion";
import { FileLabel } from "./FileLabel";

const USER_REGEX = /^[A-z][A-z0-9-_]{3,23}$/;
const PWD_REGEX = /^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#$%]).{8,24}$/;
const EMAIL_REGEX =
  /^[a-zA-Z.!#$%&'+/=?^_{|}~-][a-zA-Z0-9.!#$%&'+/=?^_{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;

function UserProfile({
  loggedUser,
  setLoggedUser,
  setUser,
  user,
  setPost,
  post,
}) {
  const [userName, setUserName] = useState(loggedUser.nick);
  const [userCheck, setUserCheck] = useState(false);
  const [userFocus, setUserFocus] = useState(false);

  const [email, setEmail] = useState(loggedUser.email);
  const [emailCheck, setEmailCheck] = useState(false);
  const [emailFocus, setEmailFocus] = useState(false);

  const [pwd, setPwd] = useState(loggedUser.password);
  const [pwdCheck, setPwdCheck] = useState(false);
  const [pwdFocus, setPwdFocus] = useState(false);

  const [firstName, setFirstName] = useState(
    loggedUser.firstName === undefined ? "" : loggedUser.firstName
  );

  const [lastName, setLastName] = useState(
    loggedUser.lastName === undefined ? "" : loggedUser.lastName
  );

  const [hint, setHint] = useState();

  const [errorMessage, setErrorMessage] = useState();

  const [selectedFile, setSelectedFile] = useState();
  const [preview, setPreview] = useState();

  useEffect(() => {
    if (userName !== undefined) {
      setUserCheck(USER_REGEX.test(userName));
    } else {
      setUserCheck(false);
    }
  }, [userName]);

  useEffect(() => {
    setPwdCheck(PWD_REGEX.test(pwd));
  }, [pwd]);

  useEffect(() => {
    setEmailCheck(EMAIL_REGEX.test(email));
  }, [email]);

  const changeUserData = (e) => {
    e.preventDefault();
    setUser(
      user.map((person) => {
        return person.id === loggedUser.id
          ? {
              id: loggedUser.id,
              nick: userName,
              email: email,
              firstName: firstName,
              lastName: lastName,
              password: pwd,
              image: preview,
            }
          : person;
      })
    );
    setLoggedUser({
      id: loggedUser.id,
      nick: userName,
      email: email,
      firstName: firstName,
      lastName: lastName,
      password: pwd,
      image: preview,
    });
    setPost(
      post.map((item) => {
        return item.userID === loggedUser.id
          ? {
              id: item.id,
              userID: item.userID,
              title: item.title,
              text: item.text,
              image: preview,
            }
          : item;
      })
    );
  };

  useEffect(() => {
    if (!selectedFile) {
      setPreview(undefined);
      return;
    }
    const objectUrl = URL.createObjectURL(selectedFile);
    setPreview(objectUrl);

    return () => URL.revokeObjectURL(objectUrl);
  }, [selectedFile]);

  const onSelectFile = (e) => {
    if (!e.target.files || e.target.files.length === 0) {
      setSelectedFile(undefined);
      return;
    }
    setSelectedFile(e.target.files[0]);
  };

  useEffect(() => {
    setPreview(loggedUser.image);
  }, []);

  return (
    <AnimatePresence>
      <UserProfileSection
        key="userp"
        initial={{ x: "-200vw", transition: { duration: 2 } }}
        animate={{ x: 0, transition: { duration: 0.4 } }}
      >
        <UserProfileInputContainer
          id="changeUserData"
          onSubmit={changeUserData}
        >
          <StyledH2>Edit profile</StyledH2>
          {selectedFile ? (
            <StyledImg
              image={preview}
              alt="avatar"
              style={{ gridColumn: "2/3" }}
            ></StyledImg>
          ) : (
            <FileLabel htmlFor="imgInput" image={loggedUser.image}>
              <FileInput
                type="file"
                id="imgInput"
                accept=".png, .jpg"
                onChange={onSelectFile}
              />
            </FileLabel>
          )}
          <SettingsInputBox grid={"1 / 3"} style={{ width: "100%" }}>
            <SettingsInput
              type="text"
              id="userName"
              onChange={(e) => setUserName(e.target.value)}
              value={userName}
              onFocus={() => {
                setUserFocus(true);
                setHint(
                  "Login musi zawierać od 4 do 24 znaków i musi zaczynć się od litery. Ltery, cyfry, podkreślniki i myślniki są dozwolone."
                );
              }}
              onBlur={() => setUserFocus(false)}
              required
            ></SettingsInput>
            <StyledSpan>Nazwa użytkownika</StyledSpan>
            <StyledSecondSpan
              style={{ display: userCheck && userName ? "block" : "none" }}
            >
              <CheckIcon />
            </StyledSecondSpan>
            <StyledSecondSpan
              style={{ display: userCheck || !userName ? "none" : "block" }}
            >
              <TimesIcon />
            </StyledSecondSpan>
          </SettingsInputBox>
          <SettingsInputBox grid={"1 / 2"}>
            <SettingsInput
              type="text"
              id="firstName"
              onChange={(e) => setFirstName(e.target.value)}
              value={firstName}
              required
            ></SettingsInput>
            <StyledSpan>Imie</StyledSpan>
          </SettingsInputBox>
          <SettingsInputBox grid={"2 / 3"}>
            <SettingsInput
              type="text"
              id="lastName"
              onChange={(e) => setLastName(e.target.value)}
              value={lastName}
              required
            ></SettingsInput>
            <StyledSpan>Nazwisko</StyledSpan>
          </SettingsInputBox>
          <SettingsInputBox grid={"1 / 3"}>
            <SettingsInput
              type="email"
              id="email"
              onChange={(e) => setEmail(e.target.value)}
              value={email}
              onFocus={() => {
                setEmailFocus(true);
                setHint("Przykładowy email: name@domain.com");
              }}
              onBlur={() => setEmailFocus(false)}
              required
            ></SettingsInput>
            <StyledSpan>Enter your email</StyledSpan>
            <StyledSecondSpan
              style={{ display: emailCheck && email ? "block" : "none" }}
            >
              <CheckIcon />
            </StyledSecondSpan>
            <StyledSecondSpan
              style={{ display: emailCheck || !email ? "none" : "block" }}
            >
              <TimesIcon />
            </StyledSecondSpan>
          </SettingsInputBox>
          <SettingsInputBox grid={"1 / 3"}>
            <SettingsInput
              type="password"
              id="password"
              onChange={(e) => setPwd(e.target.value)}
              value={pwd}
              onFocus={() => {
                setPwdFocus(true);
                setHint(
                  "Hasło musi zawierać od 8 do 24 znaków i składać się z conajmniej jednej dużej litery i znaku specjalnego. Dozwolone znaki specjalne: !, @, #"
                );
              }}
              onBlur={() => setPwdFocus(false)}
              required
            ></SettingsInput>
            <StyledSpan>Hasło</StyledSpan>
            <StyledSecondSpan style={{ display: pwdCheck ? "block" : "none" }}>
              <CheckIcon />
            </StyledSecondSpan>
            <StyledSecondSpan
              style={{ display: pwdCheck || !pwd ? "none" : "block" }}
            >
              <TimesIcon />
            </StyledSecondSpan>
          </SettingsInputBox>
          <AnimatePresence mode="wait">
            <ErrorContainer style={{ width: "90%", margin: "auto" }}>
              {(pwdFocus && !pwdCheck) ||
              (userFocus && !userCheck) ||
              (emailFocus && !emailCheck) ? (
                <StyledHint
                  key="registerHint"
                  initial={{ opacity: 0 }}
                  animate={{ opacity: 1, transition: { duration: 0.2 } }}
                  exit={{ opacity: 0, transition: { duration: 0.2 } }}
                >
                  {hint}
                </StyledHint>
              ) : (
                <></>
              )}
              <StyledError
                key="registerErrorMessage"
                style={{
                  display:
                    pwdFocus || userFocus || emailFocus ? "none" : "block",
                }}
              >
                {errorMessage}
              </StyledError>
            </ErrorContainer>
          </AnimatePresence>
        </UserProfileInputContainer>
        <StyledButton
          form="changeUserData"
          type="submit"
          color={"#fff"}
          backgroundColor={"#FF7008"}
          fontWeight={"300"}
          justify={"center"}
          style={{ gridColumn: "1/3" }}
        >
          Zapisz
        </StyledButton>
      </UserProfileSection>
    </AnimatePresence>
  );
}

export default UserProfile;
