import styled from "styled-components";

export const StyledSpan = styled.span`
  position: absolute;
  font-family: "Roboto";
  font-weight: 300;
  font-size: var(--input-fs);
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  padding: 10px;
  pointer-events: none;
  transition: 0.5s;
`;

export const StyledSecondSpan = styled.span`
  position: absolute;
  font-family: "Roboto";
  font-weight: 300;
  font-size: var(--input-fs);
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  padding: 10px;
  pointer-events: none;
  height: 50px;
`;
