import styled from "styled-components";

export const YourPosts = styled.div`
  height: 40vh;
  overflow: scroll;
  overflow-x: hidden;
  margin-top: 5vh;

  &::-webkit-scrollbar-thumb {
    background: #ffc297;
    border-radius: 25px;
  }

  &::-webkit-scrollbar {
    width: 20px;
  }
`;
